<template>
    <div class="totalline-box">
        <div class="totalline-nav-box">
            <div @click="activeTab = 0">
                <span :class="{ 'brdrbtm': activeTab == 0, 'border-green': activeTab == 0 }">
                    {{ $t('settotalline.basicInformation') }}
                </span>
            </div>
            <div @click="activeTab = 1">
                <span :class="{ 'brdrbtm': activeTab == 1, 'border-green': activeTab == 1 }">
                    {{ $t('settotalline.input') }}
                </span>
            </div>
            <div @click="activeTab = 2">
                <span :class="{ 'brdrbtm': activeTab == 2, 'border-green': activeTab == 2 }">
                    {{ $t('settotalline.output') }}
                </span>
            </div>
            <div class="totalline-line"></div>
        </div>
        <div v-if="hasPermit">
            <!-- 字节顺序 -->
            <div v-show="activeTab == 0" class="bytes-order">
                <!-- <div class="totalline-node-line">
                        <span class="totalline-node">{{$t('settotalline.nodename')}}</span>
                        <InputText class="node-input" :text="totalSetting['NodeName']" v-model="nodeName"></InputText>   
                        </div> -->
                <div class="totalline-node-line">
                    <span class="totalline-node">{{ $t('settotalline.fieldname') }}</span>
                    <span class="node-input">{{ filedbusname }}</span>
                </div>
                <div class="data-order-line">
                    <span class="data-order">{{ $t('settotalline.databyteorder') }}</span>
                    <NewSelect :module="module" :realVal="totalSetting.NumeralByteOrder" :options="numberOrders"
                        @valChng="totalSetting.NumeralByteOrder = $event" class="data-input"></NewSelect>
                </div>
                <div class="string-order-line">
                    <span class="string-order">{{ $t('settotalline.characterbyteorder') }}</span>
                    <NewSelect :module="module" :realVal="totalSetting.StringByteOrder" :options="stringOrders"
                        @valChng="totalSetting.StringByteOrder = $event" class="string-input"></NewSelect>
                </div>
            </div>

            <!-- 输入总线 -->
            <table v-show="activeTab == 1" class="input-line-table">
                <tr class="totalline-tr">
                    <td>{{ $t('settotalline.address') }}</td>
                    <td>{{ $t('settotalline.type') }}</td>
                    <td>{{ $t('settotalline.length') }}</td>
                    <td class="last-td">{{ $t('settotalline.function') }}</td>
                </tr>
                <div class="mask-box">
                    <div class="table-box">
                        <tr v-for="(item, key) in totalSetting.ToolIn.Member" :key="key"
                            :class="{ 'totalline-tr': true, 'active-input': activeInput == key }"
                            @click="showTrash('input', key)">
                            <td>{{ parseFloat(item.StartByte).toFixed(1) }}</td>
                            <td>{{ getInSdoItem(item.LinkSdo, "type") }}</td>
                            <td>{{ item.BitLength }}</td>
                            <td class="last-td">{{ getInSdoItem(item.LinkSdo, 'name') }}</td>
                            <div class="div-edit-menu" v-show="activeInput == key">
                                <svg-loader @click="delMember('ToolIn', key)" icon="DelDmrct" class="del-btn"
                                    size="2.5rem"></svg-loader>
                                <svg-loader @click="eidtFiledBus('ToolIn', item.LinkSdo)" icon="Edit" class="edit-btn"
                                    size="2.5rem"></svg-loader>
                                <svg-loader @click="up('ToolIn', key)" icon="Up" class="up-btn" size="2.5rem"></svg-loader>
                                <svg-loader @click="down('ToolIn', key)" icon="Down" class="down-btn"
                                    size="2.5rem"></svg-loader>
                            </div>
                        </tr>
                        <tr class="add-line totalline-tr">
                            <td>
                                <svg-loader @click="setFiledBus('ToolIn')" class="add-btn" icon="StaticCircleAdd"
                                    size="2.5rem"></svg-loader>
                            </td>
                            <td></td>
                            <td></td>
                            <td class="last-td"></td>
                        </tr>

                    </div>
                </div>
            </table>
            <!-- 输出总线 -->
            <table v-show="activeTab == 2" class="output-line-table">
                <tr class="totalline-tr">
                    <td>{{ $t('settotalline.address') }}</td>
                    <td>{{ $t('settotalline.type') }}</td>
                    <td>{{ $t('settotalline.length') }}</td>
                    <td class="last-td">{{ $t('settotalline.function') }}</td>
                </tr>
                <div class="mask-box">
                    <div class="table-box">
                        <tr v-for="(item, key) in totalSetting.ToolOut.Member" :key="key"
                            :class="{ 'totalline-tr': true, 'active-output': activeOutput == key }"
                            @click="showTrash('output', key)">
                            <td>{{ parseFloat(item.StartByte).toFixed(1) }}</td>
                            <td>{{ getOutSdoItem(item.LinkSdo, 'type') }}</td>
                            <td>{{ item.BitLength }}</td>
                            <td class="last-td">{{ getOutSdoItem(item.LinkSdo, 'name') }}</td>
                            <div class="div-edit-menu" v-show="activeOutput == key">
                                <svg-loader @click="delMember('ToolOut', key)" icon="DelDmrct" class="del-btn"
                                    size="2.5rem"></svg-loader>
                                <svg-loader @click="eidtFiledBus('ToolOut', item.LinkSdo)" icon="Edit" class="edit-btn"
                                    size="2.5rem"></svg-loader>
                                <svg-loader @click="up('ToolOut', key)" icon="Up" class="up-btn" size="2.5rem"></svg-loader>
                                <svg-loader @click="down('ToolOut', key)" icon="Down" class="down-btn"
                                    size="2.5rem"></svg-loader>
                            </div>
                        </tr>
                        <tr class="add-line totalline-tr">
                            <td><svg-loader @click="setFiledBus('ToolOut')" class="add-btn" icon="StaticCircleAdd"
                                    size="2.5rem"></svg-loader></td>
                            <td></td>
                            <td></td>
                            <td class="last-td"></td>
                        </tr>
                    </div>
                </div>

            </table>
        </div>
        <LeetxButton :enablerun="false" :module="module" class="confirm-box" textColor="#fff" bgcColor="#00c155"
            width="60.625vw" height="3.75rem" fontSize="2.25rem" :text="$t('confirm.confirm')" @click="submitTotalData">
        </LeetxButton>
    </div>
</template>
<script>
import editFieldBusDialog from '../../dialogs/settings/fieldbus/fieldBus'
import NewSelect from '../select/NewSelect.vue'
import { getWhoSdoItems, getTypeCode } from '../../dialogs/settings/fieldbus/fieldbusDefine'
import { busFiledbusCfgDownloadApi, busFieldbusCfgUploadApi } from '../../api/io/io'
import { messageApi } from '../message/message'
import LeetxButton from '../controls/LeetxButton.vue'
import cnfrmWin from '../../dialogs/common/confirm/cnfrm.js'
import { deepCopy } from '../../utils/deepCopy'
export default {
    components: {
        // InputText,
        LeetxButton, NewSelect
    },
    data() {
        return {
            module: "DEV_BUS",
            activeTab: 0,
            nodeName: {},
            totalSetting: {
                "Type": "FieldBus",
                "NumeralByteOrder": "1234",
                "StringByteOrder": "ABCD",
                // "NodeName": "testNode",
                "ToolIn": {
                    "ByteLength": 220,
                    "Member": []
                },
                "ToolOut": {
                    "ByteLength": 220,
                    "Member": [
                    ]
                }
            },
            stringOrders: {
                "ABCD": "ABCD",
                "DCBA": "DCBA",
                "CDAB": "CDAB",
            },
            numberOrders: {
                "1234": "1234",
                "3412": "3412",
                "4321": "4321",
            },
            activeInput: -1,
            activeOutput: -1,
            hasPermit: true,
            addrMask: {
                ToolIn: null,
                ToolOut: null,
            },
            sdoItems: {
                "ToolIn": {},
                "ToolOut": {},
            },
            oldSetting: {
                "Type": "FieldBus",
                "NumeralByteOrder": "1234",
                "StringByteOrder": "ABCD",
                // "NodeName": "testNode",
                "ToolIn": {
                    "ByteLength": 220,
                    "Member": []
                },
                "ToolOut": {
                    "ByteLength": 220,
                    "Member": [
                    ]
                }
            },
            // isAllowLeave: true,
            isFirstChange: true,
            filedbusTypes: {
                0: "Undetected",
                1: "Profinet",
                2: "Ethercat"
            }
        }
    },
    methods: {
        up(key, index) {
            if (index > 0) {
                this.totalSetting[key].Member[index - 1] = this.totalSetting[key].Member.splice(index, 1, this.totalSetting[key].Member[index - 1])[0]
            }
        },
        down(key, index) {
            if (index < this.totalSetting[key].Member.length - 1) {
                this.totalSetting[key].Member[index] = this.totalSetting[key].Member.splice(index + 1, 1, this.totalSetting[key].Member[index])[0]
                // console.log(key, index);
            }
        },
        // 弹出总线配置对话框
        setFiledBus(key) {
            let sdoType = key == "ToolIn" ? 'in' : 'out'
            let sdoItems = this.sdoItems[key]
            let props = { sdoItems, mask: this.addrMask[key], key, sdoType }
            editFieldBusDialog(props).then((result) => {
                this.addMember(key, result)
            }).catch(err => {
                console.log(err);
            })
        },
        eidtFiledBus(key, sdo) {
            let sdoType = key == "ToolIn" ? 'in' : 'out'
            let sdoItems = this.sdoItems[key]
            let curItem = undefined
            let index = key == "ToolIn" ? this.activeInput : this.activeOutput
            // 地址
            let address = this.totalSetting[key].Member[index].StartByte
            // 拿取字节和位
            let byte = Math.floor(address);
            let bit = Math.round((address - byte) * 10);
            // 删除地址
            for (let i = 0; i < this.totalSetting[key].Member[index].BitLength; i++) {
                this.addrMask[key][byte * 8 + bit + i] = 0
            }
            // 当前编辑的信号设置为可用
            sdoItems[sdo].optional = true
            curItem = {
                sdo: sdo,
                byte: byte,
                bit: bit,
                len: this.totalSetting[key].Member[index].BitLength,
                group: sdoItems[sdo].group,
                type: sdoItems[sdo].type
            }
            // console.log('curItem',curItem );
            let props = { sdoItems, mask: this.addrMask[key], sdoType, curItem }
            // console.log(props);
            editFieldBusDialog(props).then((config) => {
                let type = getTypeCode(config.type)
                // console.log( config  );
                let BitLength = config.len
                let newBusItem = { "StartByte": config.byte + config.bit * 0.1, BitLength, "LinkSdo": config.sdo, type }
                this.totalSetting[key].Member[index] = newBusItem
                for (let i = 0; i < config.len; i++) {
                    this.addrMask[key][Math.round(config.byte * 8 + config.bit + i)] = 1
                }
                // 信号被占用
                this.updateSdoItemsOptional(key, config.sdo, false);

            }).catch(() => {
                for (let i = 0; i < this.totalSetting[key].Member[index].BitLength; i++) {
                    this.addrMask[key][byte * 8 + bit + i] = 1
                }
                this.updateSdoItemsOptional(key, sdo, false);
            })
        },
        // 需要获取总线数据
        //提交数据
        submitTotalData() {
            console.log("this.totalSetting", this.totalSetting);

            busFiledbusCfgDownloadApi(this.totalSetting).then((result) => {
                if (result.status == 0) {
                    this.oldSetting = deepCopy(this.totalSetting)
                    messageApi.success(["保存成功"])
                } else {
                    // status:9102
                    messageApi.error([`保存失败,错误代码${result.status}`])
                }
            }).catch(() => {
                // console.log(e);
                messageApi.error(["系统下发配置异常,保存失败"])
            });
        },
        // 展示删除按钮
        showTrash(key, val) {
            if (key == 'input') {
                this.activeInput = this.activeInput == val ? -1 : val
            }
            else {
                this.activeOutput = this.activeOutput == val ? -1 : val
            }
        },
        // 添加总线配置项
        addMember(key, config) {
            let type = getTypeCode(config.type)
            let BitLength = config.len
            this.totalSetting[key].Member.push({ "StartByte": config.byte + config.bit * 0.1, BitLength, "LinkSdo": config.sdo, type })
            for (let i = 0; i < config.len; i++) {
                this.addrMask[key][Math.round(config.byte * 8 + config.bit + i)] = 1
            }
            // 信号被占用
            this.updateSdoItemsOptional(key, config.sdo, false)
        },
        // pushAddrMask(byte, bit, len, val) {
        //     for (let i = 0; i < len; i++) {
        //         this.addrMask[byte * 8 + bit + i] = val;
        //     }
        // },
        // 删除总线配置项
        delMember(key, i) {
            let title = this.$t('dbgdmrct.deleteConfigurate')
            let confirmText = this.$t('dbgdmrct.yes')
            let cancelText = this.$t('dbgdmrct.no')
            let msg = this.$t('settotalline.confirmDeletion')
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {
                // 删除配置
                let item = this.totalSetting[key].Member[i];
                let byte = Math.floor(item.StartByte);
                let bit = Math.round((item.StartByte - byte) * 10);
                let sdo = this.totalSetting[key].Member[i].LinkSdo
                this.totalSetting[key].Member.splice(i, 1)
                for (let i = 0; i < item.BitLength; i++) {
                    this.addrMask[key][byte * 8 + bit + i] = 0;
                }
                this.updateSdoItemsOptional(key, sdo, true)
            })

        },
        // 获取输入总线配置项
        getInSdoItem(sdo, key) {
            let item = this.sdoItems['ToolIn'][sdo]
            return item ? item[key] : undefined
        },
        // 获取输出总线配置项
        getOutSdoItem(sdo, key) {
            let item = this.sdoItems['ToolOut'][sdo]
            if (item) {
                return item[key]
            }
            return ""
        },
        // 初始化总线配置
        initSdoItems() {
            this.sdoItems['ToolIn'] = getWhoSdoItems('in')
            this.sdoItems['ToolOut'] = getWhoSdoItems('out')
        },
        // 初始化已使用的地址
        initAddress(data) {
            // console.log(data);
            let toolin = data["ToolIn"].Member
            let toolout = data["ToolOut"].Member
            for (let i = 0; i < toolin.length; i++) {
                let item = toolin[i]
                let byte = Math.floor(item.StartByte);
                let bit = Math.round((item.StartByte - byte) * 10);

                for (let i = 0; i < item.BitLength; i++) {
                    this.addrMask.ToolIn[byte * 8 + bit + i] = 1;
                }
            }
            for (let i = 0; i < toolout.length; i++) {
                let item = toolout[i]
                let byte = Math.floor(item.StartByte);
                let bit = Math.round((item.StartByte - byte) * 10);
                // console.log(byte,bit,item);
                for (let i = 0; i < item.BitLength; i++) {
                    this.addrMask.ToolOut[byte * 8 + bit + i] = 1;
                }
            }
        },
        // 从后台拿总线数据
        async getFieldbusData() {
            await busFieldbusCfgUploadApi().then((result) => {
                if (result.status == 0) {
                    if (result.data['Type'] == undefined) {
                        let _ToolIn = result.data.ToolIn
                        let _ToolOut = result.data.ToolOut
                        if (!_ToolIn) {
                            _ToolIn = {
                                "ByteLength": 220,
                                "Member": []
                            }
                        }
                        if (!_ToolOut) {
                            _ToolOut = {
                                "ByteLength": 220,
                                "Member": []
                            }
                        }
                        this.totalSetting = {
                            "Type": "FieldBus",
                            "NumeralByteOrder": "1234",
                            "StringByteOrder": "ABCD",
                            ToolIn: _ToolIn,
                            ToolOut: _ToolOut,
                        }
                    } else {
                        this.totalSetting = result.data
                    }
                    // console.log(' this.totalSetting', this.totalSetting);

                    this.initSdoItemsOptional();
                    this.initAddress(this.totalSetting);
                    this.oldSetting = deepCopy(this.totalSetting);

                } else {
                    messageApi.error(["获取总线数据失败"])
                }
            }).catch(() => {
                this.oldSetting = deepCopy(this.totalSetting)
            });
        },
        // 更新sdoItems 可选状态
        updateSdoItemsOptional(group, key, optional) {
            if (key in this.sdoItems[group]) this.sdoItems[group][key].optional = optional
            else console.log(group, key, optional);
        },
        initSdoItemsOptional() {
            this.totalSetting.ToolIn.Member.forEach((x) => {
                this.updateSdoItemsOptional("ToolIn", x.LinkSdo, false)
            })
            this.totalSetting.ToolOut.Member.forEach((x) => {
                this.updateSdoItemsOptional("ToolOut", x.LinkSdo, false)
            })
        }
    },
    created() {
        this.addrMask.ToolIn = new Int8Array(160);
        this.addrMask.ToolOut = new Int8Array(160);
        this.initSdoItems()
        this.getFieldbusData()
    },
    beforeRouteLeave(to, from, next) {
        if (JSON.stringify(this.oldSetting) === JSON.stringify(this.totalSetting) || this.$store.state.g_status.busy) {
            next();
            return;
        }
        let title = this.$t('setcontrol.saveSet')
        let confirmText = this.$t('setcontrol.yes')
        let cancelText = this.$t('setcontrol.no')
        let msg = this.$t('settotalline.isSaveBusSet')
        // let msg = "是否保存总线设置"
        cnfrmWin({
            title,
            confirmText,
            cancelText,
            msg
        }).then(() => {
            busFiledbusCfgDownloadApi(this.totalSetting).then((result) => {
                if (result.status == 0) {
                    let msg = this.$t('settable.success')
                    messageApi.success([msg])
                } else {
                    messageApi.error([`保存失败,错误代码${result.status}`])
                }
            }).catch(() => {
                // console.log(e);
                messageApi.error(["系统下发配置异常,保存失败"])
            }).finally(() => {
                next()
            })
        }).catch(() => {
            next()
        });
    },
    computed: {
        enabled() {
            if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.5;
        },
        filedbusname() {
            if (this.$store.state.g_status.anybustype in this.filedbusTypes)
                return this.filedbusTypes[this.$store.state.g_status.anybustype]
            else
                return "NotExist";
        }
    },
    watch: {
        // totalSetting: {
        //     deep: true,
        //     handler: function () {
        //         if (!this.isFirstChange) {
        //             this.isAllowLeave = Object.is(this.oldSetting, this.totalSetting)
        //         }
        //     }
        // }
    }
}
</script>
<style scoped>
.totalline-box {
    position: relative;
    top: 0.5rem;
    height: 100%;
    width: 100%;

}

.totalline-nav-box {
    display: flex;
    width: 45.125vw;
    position: relative;
    left: 50%;
    height: 3.7387vh;
    line-height: 3.7387vh;
    transform: translateX(-50%);
    font-size: var(--font1_5);
}

.totalline-nav-box>div {
    flex: 1;
}

.bytes-order {
    font-size: var(--font1_625) !important;
    position: relative;
    top: 1rem;
}

.totalline-node-line,
.data-order-line,
.string-order-line {
    position: absolute;
    height: 3.9063vh;
    width: 100%;
    top: 2.6961vh;

}

.totalline-node,
.data-order,
.string-order {
    position: absolute;
    left: 7.4375vw;
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font1_625);
    color: var(--C3B);
}

.node-input,
.data-input,
.string-input {
    position: absolute;
    right: 7.4375vw;
}

.data-order-line {
    top: 7.7742vh;
}

.string-order-line {
    top: 12.8125vh;
}

.confirm-box {
    position: absolute;
    bottom: 4rem;
    left: 50%;
    transform: translateX(-50%);
}

.brdrbtm {
    position: relative;
    box-sizing: border-box;
    color: var(--C00) !important;
    font-weight: 600;
    white-space: nowrap !important;
    text-overflow: ellipsis
}

.totalline-line {
    position: absolute;
    top: calc(2.4rem + 4px);
    width: calc(30vw + 6rem);
    left: 50%;
    transform: translateX(-50%);
    height: 1px;
    background-color: var(--CD4);
    z-index: -1;
}

.border-green {
    box-sizing: border-box;
    border-bottom: 4px solid var(--C00c155);
}

.mask-box {
    position: relative;
    width: calc(74.5vw + 5.6rem);
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    height: 52vh;
}

.table-box {
    position: relative;
    left: 0;
    top: 0;
    height: 100%;
    width: calc(74.5vw + 5.6rem + 18px);
    overflow-y: scroll;
    overflow-x: hidden;
}

.input-line-table,
.output-line-table {
    position: relative;
    top: 20px;
    left: 7.75vw;
    width: calc(74.5vw + 5.6rem);
    color: var(--C3B);
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
}

.input-line-table>tr:first-child,
.output-line-table>tr:first-child {
    border-top: 2px solid var(--C00);
    border-bottom: 2px solid var(--C00);
}

.active-input,
.active-output {
    border: 1px solid var(--C00c155) !important;
    background: rgba(138, 216, 121, 0.1) !important;
    box-sizing: border-box !important;
}

.totalline-tr {
    position: relative;
    left: 0;
    width: 74.5vw;
    display: flex;
    border: 1px solid transparent;
    border-bottom: 1px solid var(--Cd4);
    box-sizing: border-box;
}

.totalline-tr td {
    font-size: var(--font1_625);
    flex: 1;
    height: 4.2969vh;
    line-height: 4.2969vh;
    overflow: hidden;
    text-overflow: ellipsis;
}

.totalline-tr .last-td {
    flex: 2;
}

.edit_box {
    width: 3.75rem;
    height: 15rem;
    background-color: #EEEEEE;
    border: 1px solid #FFFFFF;
}

.div-edit-menu {
    position: absolute;
    right: -5rem;
    width: 4rem;
    height: 16rem;
    background: #eee;
    border-radius: 0.8rem;
    border: 2px solid #fff;
    box-shadow: inset 0px 0px 20px #eee,
        0px 10px 20px #dfdfdf;
}

.edit-btn {
    position: absolute;
    top: 0.5rem;
    left: 0.9rem;
}

.up-btn {
    position: absolute;
    top: 4.5rem;
    color: red;
    left: 0.9rem;
}

.down-btn {
    position: absolute;
    top: 8.5rem;
    color: red;
    left: 0.9rem;
}

.del-btn {
    position: absolute;
    top: 12.5rem;
    left: 0.9rem;
    color: red;
}

.add-btn {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
