<template>
    <div class="totalline-box" v-if="isShow">
        <div class="totalline-title-line">
            <span class="totalline-title-msg">{{ $t('dlgtotalline.totalline') }}</span>
            <span class="totalline-box-close" @click="cancel">
                <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="totalline-group-line">
            <div class="totalline-system-text">{{ $t('dlgtotalline.group') }}</div>
            <NewSelect :realVal='activeGroup' @valChng="activeGroup = $event" :options="groups"
                class="totalline-select-system"></NewSelect>
        </div>
        <div class="totalline-system-line">
            <div class="totalline-system-text">{{ $t('dlgtotalline.selectFunc') }}</div>
            <BusSelect :realVal='activeSdo' :isValid="isValid" :validReason="$t('dlginputset.configUsing')"
                @valChng="updateBusItem($event)" :options="items[activeGroup]"
                :class="{ 'totalline-select-system': true }"></BusSelect>
        </div>
        <div class="totalline-signal-line">
            <div class="totalline-signal-text">{{ $t('dlgtotalline.byte') }}</div>
            <div :class="{ 'totalline-select-signal': true }">
                <Counter :cnum="fieldbusItem.byte" style="float:left" :_min="0" :_max="219"
                    @update="updateNum($event, 'byte')"></Counter>
                <span class="totalline-bit-msg">{{ $t('dlgtotalline.bit') }}</span>
                <Counter :cnum="fieldbusItem.bit" :textValid="validAddress == ''" :validReason="validAddress"
                    style="float:right" :_min="minBit" :_max="maxBit" @update="updateNum($event, 'bit')"></Counter>
            </div>
        </div>

        <div class="totalline-delay-line">
            <div class="totalline-delay-text">{{ $t('dlgtotalline.length') }}</div>
            <div class="totalline-select-signal">
                <Counter :cnum="fieldbusItem.len" _width="100%" :_min="minLen" :_max="maxLen" :_value="8"
                    @update="updateNum($event, 'len')"></Counter>
            </div>
        </div>
        <div class="totalline-operate-line">
            <div class="totalline-cancel-btn" @click="cancel">{{ $t('dlgtotalline.cancel') }}</div>
            <div class="totalline-cnfrm-btn" @click="updateTotalLineSetting">{{ $t('dlgtotalline.sure') }}</div>
        </div>
    </div>
</template>
<script>
import svgloader from '@/components/icon_font/svgloader.vue'
import Counter from "@/components/controls/Counter.vue"
import NewSelect from '@/components/select/NewSelect.vue'
import BusSelect from '@/components/select/BusSelect.vue'
import { maskApi } from '../../mask/StackMask.vue'
import { deepCopy } from '@/utils/deepCopy'
// import { typeLength } from './fieldbusDefine'
export default {
    components: { svgloader, Counter, NewSelect, BusSelect },
    props: {
        // 地址列表
        mask: {
            type: Int8Array,
            default: null
        },
        sdoItems: {
            required: true,
            type: Object,
            default: function () {
                return {}
            }
        },
        sdoType: {
            type: String,
            default: "in"
        },
        curItem: {
            type: Object,
            default: function () {
                return {
                    sdo: undefined,
                    byte: 0,
                    bit: 0,
                    len: 6,
                    group: "sys_status",
                    type: "Bool,"
                }
            }
        }

    },
    data() {
        return {
            validAddress: '',
            bits: [1, 2, 3, 4, 5, 6, 7, 8],
            fieldbusItem: {
                sdo: undefined,
                byte: 1,
                bit: 0,
                len: 6,
                group: "sys_status",
                type: "Bool,"
            },
            groups: {},
            items: {},
            isShow: true,
            activeGroup: undefined,
            activeSdo: "0x300101",
            minBit: 0,
            maxBit: 7,
            minLen: 1,
            maxLen: 1760,
            curType: "Bool",
            maskId: 0,
        }
    },
    methods: {
        updateTotalLineSetting() {
            if (!this.allowConfirm) { return }
            this.fieldbusItem.sdo = this.activeSdo
            this.fieldbusItem.group = this.activeGroup
            this.fieldbusItem.type = this.sdoItems[this.activeSdo].type
            this.onConfirm && this.onConfirm(this.fieldbusItem)
            this.isShow = false
            maskApi.descend(this.maskId)
        },
        cancel() {
            maskApi.descend(this.maskId)
            this.isShow = false
            this.onCancel && this.onCancel()
        },
        updateNum(e, key) {
            // console.log(e, key);
            this.fieldbusItem[key] = e

        },
        initStatus() {
            const cancel = this.cancel
            this.maskId = maskApi.rise(15, cancel)
        },
        addrSuggestion() {
            if (this.fieldbusItem.type == "Bool") {
                for (let bIndex = 0; bIndex < 220; bIndex++) {
                    for (let i = 0; i < 8; i++) {
                        if (this.mask[bIndex * 8 + i] == 0) {
                            this.fieldbusItem.bit = i;
                            this.fieldbusItem.byte = bIndex;
                            return;
                        }
                    }
                }
            }
            else {
                let findex = 0;
                let len = parseInt(this.sdoItems[this.activeSdo].len / 8)
                for (let bIndex = 0; bIndex < 220; bIndex++) {
                    for (let i = 0; i < 8; i++) {
                        if (this.mask[bIndex * 8 + i] == 1) {
                            findex = -1;
                            break;
                        }
                    }
                    // console.log(bIndex, findex, len);
                    if (len % 2 == 0 && findex == -1 && bIndex % 2 == 0) {
                        bIndex += 1;
                    }
                    findex += 1;
                    if (findex == len) {
                        this.fieldbusItem.byte = bIndex - findex + 1;
                        break;
                    }
                }
            }
        },
        updateBusItem(data) {
            this.fieldbusItem.len = data.len;
            //    this.config.len = data.len;
            this.fieldbusItem.type = data.type;
            this.fieldbusItem.name = data.name;
            this.fieldbusItem.sdo = data.val;
            this.activeSdo = data.val;
            this.addrSuggestion();
        },
        initGroupsAndItems() {
            this.groups = {}
            this.items = {}
            for (let val in this.sdoItems) {
                let item = this.sdoItems[val]
                let group = this.sdoItems[val].group
                let name = this.sdoItems[val].name
                if (this.groups[group] == undefined) {
                    this.groups[group] = group
                    this.items[group] = {}
                    this.items[group][name] = { val, ...item }
                }
                else {
                    this.items[group][name] = { val, ...item }
                }
            }
        },
        initConfig() {
            const sdo = this.curItem.sdo
            if (sdo) {
                this.activeGroup = this.curItem.group
                this.activeSdo = this.curItem.sdo
                this.fieldbusItem = deepCopy(this.curItem)
            }
            else if (this.sdoType == "in") {
                this.fieldbusItem = { sdo: "0x300101", bit: 0, type: "Bool", len: 1, byte: 0, group: "sys_status" }
                this.activeGroup = "sys_status"
                this.activeSdo = "0x300101"
            } else if (this.sdoType == "out") {
                this.fieldbusItem = { sdo: "0x300204", bit: 0, type: "Bool", len: 1, byte: 0, group: "status" }
                this.activeGroup = "status"
                this.activeSdo = "0x300204"
            }
        }

    },

    watch: {
        activeGroup: {
            handler: function (newval, oldval) {
                if (newval != oldval && oldval != undefined) {
                    let newGroup = this.items[newval]
                    let firstKey = Object.keys(newGroup)[0];
                    this.activeSdo = newGroup[firstKey].val;
                }
            }

        },
        activeSdo: {
            handler: function () {
                this.curType = this.sdoItems[this.activeSdo].type
                if (this.curType != "Bool") {
                    this.minBit = 0
                    this.fieldbusItem.bit = 0
                    this.maxBit = 0
                }
                else {
                    this.minBit = 0
                    this.maxBit = 7
                }
                if (this.curType != "String") {
                    this.minLen = this.sdoItems[this.activeSdo].len
                    this.maxLen = this.sdoItems[this.activeSdo].len
                }
                else {
                    // this.minLen = 1
                    // this.maxLen = this.sdoItems[this.activeSdo].len
                    this.minLen = 8
                    this.maxLen = 1024
                }
            }
        },
        /**
         * 总线规则 校验
         */
        fieldbusItem: {
            handler(new_item) {
                for (let i = 0; i < new_item.len; i++) {
                    if (this.mask[new_item.byte * 8 + new_item.bit + i] == 1) {
                        this.validAddress = `${(new_item.byte + Math.floor((new_item.bit + i) / 8) + (new_item.bit + i) % 8 * 0.1).toFixed(1)}已占用`;
                        return;
                    }
                }
                if (new_item.len == 16 || new_item.len == 32) {
                    if (Math.round(new_item.byte % 2) == 1) {
                        this.validAddress = `${new_item.byte}.${new_item.bit} 起始地址不能为奇数`;
                        return;
                    }
                }
                if ((new_item.len + new_item.byte * 8 + new_item.bit) > 1760) {
                    this.validAddress = `${new_item.byte}.${new_item.bit} 所选地址长度不够`;
                    return;
                }
                this.validAddress = '';
            },
            deep: true,
            // immediate: true,
        }
    },
    created() {
        this.initGroupsAndItems()
        this.initConfig()
        this.initStatus()
    },
    computed: {
        // validAddress() {
        //     for (let i = 0; i < this.fieldbusItem.len; i++) {
        //         if (this.mask[this.fieldbusItem.byte * 8 + this.fieldbusItem.bit + i] == 1)
        //             return `${(this.fieldbusItem.byte + Math.floor((this.fieldbusItem.bit + i) / 8) + (this.fieldbusItem.bit + i) % 8 * 0.1).toFixed(1)}已占用`
        //     }
        //     if (this.fieldbusItem.len == 16 || this.fieldbusItem.len == 32) {
        //         if (this.fieldbusItem.byte % 2 == 1) {
        //             return `${this.fieldbusItem.byte}.${this.fieldbusItem.bit} 起始地址不能为奇数`
        //         }
        //     }
        //     if ((this.fieldbusItem.len + this.fieldbusItem.byte * 8 + this.fieldbusItem.bit) > 160) {
        //         return `${this.fieldbusItem.byte}.${this.fieldbusItem.bit} 所选地址长度不够`;
        //     }
        //     return '';
        // },
        isValid() {
            return this.sdoItems[this.activeSdo].optional
        },
        allowConfirm() {
            return this.isValid && this.validAddress == ''
        }
    },

}
</script>
<style scoped>
.totalline-box {
    width: 37.5rem;
    height: 31.25rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    z-index: 40;
}

.totalline-title-line {
    position: absolute;
    top: 0;
    width: 100%;
    height: 3.2688rem;
    background-color: var(--Cee);
    color: var(--C65);
    border-radius: 0.375rem;
}

.totalline-title-msg {
    position: absolute;
    display: inline-block;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    height: 2.4375rem;
    font-size: var(--font1_75);
    line-height: 2.4375rem;
    letter-spacing: 0px;
    color: var(--C3B);
}

.totalline-box-close {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3rem;
    height: 3rem;
    color: var(--C65);

}

.icon-close {
    position: absolute;
    right: 1.0625rem;
    top: 50%;
    transform: translateY(-50%);
}

.totalline-group-line {
    position: absolute;
    width: 100%;
    top: 5rem;
    height: 3.2688rem;
}

.totalline-system-line {
    position: absolute;
    width: 100%;
    top: 10rem;
    height: 3.2688rem;

}

.totalline-is-delay {
    position: absolute;
    top: 50%;
    transform: translateY(-70%);
    left: 2rem;
}

.totalline-system-text,
.totalline-signal-text,
.totalline-delay-text {
    position: absolute;
    left: 3.23rem;
    top: 50%;
    transform: translateY(-50%);
    height: 1.5625rem;

    font-size: var(--font1_625);
    line-height: 1.5625rem;
    color: var(--C3B);
}

.totalline-select-system,
.totalline-select-signal {
    position: absolute;
    height: 100%;
    width: 17.625rem;
    text-align: center;
    font-size: 1.125rem;
    right: 3.2106rem;
    border-radius: 0.25rem;
    background-color: var(--fff);
    color: var(--C5A);
    border: 1px solid var(--Cf1);
}

.nvalid-sdo {
    border: 2px solid red !important;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3) !important;
}

.totalline-signal-line {
    position: absolute;
    width: 100%;
    top: 15.2375rem;
    height: 3.2688rem;
}

.totalline-select-signal {
    display: flex;
    border: none;
}

.valid-address {
    border: 1px solid red;
}

.totalline-bit-msg {
    flex: 1;
    position: relative;
    height: 3.2688rem;
    line-height: 3.2688rem;
    font-size: var(--font1_625);
}

.totalline-delay-line {
    position: absolute;
    width: 100%;
    top: 20.575rem;
    height: 3.2688rem;

}

.program-name-input {
    position: absolute;
    height: 100%;
    width: 18.4768rem;
    text-align: center;
    font-size: 1.125rem;
    right: 3.2106rem;
    border-radius: 0.25rem;
    color: var(--C5A);
    background-color: var(--fff);
    border: 1px solid var(--Cf1);
    box-sizing: border-box;
}

.totalline-delay-unit {
    position: absolute;
    font-size: 1.125rem;
    top: 50%;
    transform: translateY(-50%);
    color: var(--C5A);
    left: 35rem;
}

.totalline-operate-line {
    position: absolute;
    width: 100%;
    bottom: 2.45rem;
    height: 3.125rem;
}

.totalline-cnfrm-btn {
    text-align: center;
    position: absolute;
    top: 0;
    height: 3.125rem;
    line-height: 3.125rem;
    width: 8.625rem;
    right: 8.4375rem;
    background-color: var(--C00c155);
    font-size: var(--font1_625);
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius: 0.25rem;
}

.totalline-cancel-btn {
    text-align: center;
    position: absolute;
    top: 0;
    height: 3.125rem;
    line-height: 3.125rem;
    width: 8.625rem;
    left: 8.4375rem;
    background-color: var(--Cf1);
    font-size: var(--font1_625);
    color: var(--C00);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius: 0.25rem;
}
</style>